import React, { FC } from "react";
import styled from "styled-components";

const Container = styled.div`
  background: #393939;
  padding: 56px 128px;
  display: flex;
  justify-content: space-between;

  @media (max-width: 1440px) {
    padding: 56px 20px;
  }
`;

const LeftSide = styled.div``;

const RightSide = styled.div`
  display: flex;

  & .column:not(:last-child) {
    margin-right: 120px;

    @media (max-width: 1440px) {
      margin-right: 30px;
    }
  }
`;

const SecondaryText = styled.p`
  opacity: 0.5;
`;

const Link = styled.p`
  opacity: 0.5;
  cursor: pointer;
`;

const TextElement = styled.span`
  letter-spacing: 0.3em;
  text-transform: uppercase;
`;

const Title = styled.h2`
  font-size: 22px;
  margin: 0;
  text-transform: uppercase;
`;

const List = styled.ul`
  list-style: none;
  padding: 0;
`;

const ColumnContainer = styled.div``;

const columns = [
  {
    title: "Контакты",
    elements: [
      "+7 (999) 804-92-32",
      "+7 (999) 804-92-32",
      "+7 (999) 804-92-32",
    ],
  },
  {
    title: "Почта",
    elements: ["car@delivery.ru", "car@delivery.com"],
  },
  {
    title: "навигация",
    elements: ["Главная", "Наши работы", "Контакты"],
    ids: ["main", "galary", "contacts"],
  },
];

const Footer: FC = () => {
  return (
    <Container id="contacts">
      <LeftSide>
        <TextElement>elite car delivery</TextElement>
        <SecondaryText>г.Москва, ул. Кутузовский проспект, дом 2</SecondaryText>
      </LeftSide>
      <RightSide>
        {columns.map(({ title, elements, ids }) => (
          <Column
            className="column"
            title={title}
            elements={elements}
            ids={ids}
          />
        ))}
      </RightSide>
    </Container>
  );
};

const scroll = (id: string) => {
  document.getElementById(id)?.scrollIntoView();
};

interface ColumnProps {
  title: string;
  elements: string[];
  className: string;
  ids?: string[];
}

const Column: FC<ColumnProps> = ({ title, elements, className, ids }) => {
  return (
    <ColumnContainer className={className}>
      <Title>{title}</Title>
      <List>
        {elements.map((elemnt, index) => {
          const id = ids ? ids[index] : null;
          return (
            <li>
              {id ? (
                <Link onClick={() => scroll(id)}>{elemnt}</Link>
              ) : (
                <SecondaryText>{elemnt}</SecondaryText>
              )}
            </li>
          );
        })}
      </List>
    </ColumnContainer>
  );
};

export default Footer;
