import React, { FC } from "react";
import Slider from "react-slick";
import styled from "styled-components";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const settings = {
  centerPadding: "100px",
  infinite: true,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 3,
  arrows: false,
  responsive: [
    {
      breakpoint: 1740,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        initialSlide: 2,
      },
    },
    {
      breakpoint: 1270,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: 1,
      },
    },
  ],
};

const StyledSlider = styled(Slider)`
  margin: 0 -28px;

  @media (max-width: 1740px) {
    margin: 0;
  }
`;

const Container = styled.div`
  margin-bottom: 155px;
`;

const SlideElement = styled.div`
  background: transparent;
`;

const Title = styled.h2`
  font-size: 52px;
  line-height: 66px;
`;

const Img = styled.img`
  border-radius: 10px;
  margin-right: 30px;
`;

interface RowProps {
  title: string;
  className?: string;
  imgs: Array<{ x1: string; x2: string }>;
}

const Row: FC<RowProps> = ({ title, className, imgs }) => {
  return (
    <Container className={className}>
      <Title>{title}</Title>
      <StyledSlider {...settings}>
        {imgs.map((img) => {
          return (
            <SlideElement>
              <Img src={img.x1} srcSet={`${img.x1} 1x, ${img.x2} 2x`} alt="" />
            </SlideElement>
          );
        })}
      </StyledSlider>
    </Container>
  );
};

export default Row;
