import React from "react";
import { FC } from "react";
import styled from "styled-components";

import bigCar1 from "../../Images/bigCar1.png";
import bigCar2 from "../../Images/bigCar2.png";
import bigCar1x2 from "../../Images/bigCar1x2.png";
import bigCar2x2 from "../../Images/bigCar2x2.png";

const Container = styled.div`
  margin-bottom: 208px;
`;

const Title = styled.h2`
  font-size: 52px;
  line-height: 66px;
  margin: 0;
  margin-bottom: 110px;
`;

const QuestionsList = styled.ul`
  font-size: 32px;
  margin-bottom: 0;
`;

const StyledLi = styled.li`
  max-width: 968px;

  & img {
    margin-right: 45px;
    margin-bottom: 30px;
  }
`;

const QuestionTitle = styled.h2`
  margin: 0;
  margin-bottom: 12px;
  font-size: 32px;
  line-height: 41px;
`;

const Text = styled.p`
  font-size: 22px;
  line-height: 28px;
  opacity: 0.7;
`;

const questions = [
  {
    title: "Кто грузит автомобиль?",
    text: "Погрузку осуществляет водитель-профессионал.",
  },
  {
    title: "Какие документы нужны для перевозки автомобиля?",
    text: "Для физического лица – свидетельство о регистрации транспортного средства (СРТС) и страховка «зелёная карта». Для юридического лица - свидетельство о регистрации транспортного средства (СРТС), страховка «зелёная карта» и доверенность на фирменном бланке организации. Если автомобиль на транзитных номерах – экспортная декларация, транзитная декларация Т1 (открывается перевозчиком при въезде в страны ЕС и закрывается в таможенном пункте страны назначения)",
  },
  {
    title: "Какие способы оплаты?",
    text: "Безналичный перевод денежных средств на расчётный счёт перевозчика, наличная оплата, денежный перевод, перевод на карту.",
  },
  {
    title: "Как оформляются документы?",
    text: "Договор на перевозку, акт осмотра при погрузке с предоставлением фотоотчета, отчёт в режиме on-line о местонахождении Вашего автомобиля, акт осмотра при выгрузке, акт выполненных работ.",
  },
  {
    title: "Чем перевозим и какой фургон?",
    text: "6 м длина 2,40 м высота",
    imgs: [
      { x1: bigCar1, x2: bigCar1x2 },
      { x1: bigCar2, x2: bigCar2x2 },
    ],
  },
];

const Questions = () => {
  return (
    <Container>
      <Title>Часто задаваемые вопросы</Title>
      <QuestionsList>
        {questions.map(({ title, text, imgs }) => (
          <Question title={title} text={text} imgs={imgs} />
        ))}
      </QuestionsList>
    </Container>
  );
};

interface QuestionProps {
  title: string;
  text: string;
  imgs?: Array<{ x1: string; x2: string }>;
}

const Question: FC<QuestionProps> = ({ text, title, imgs }) => {
  return (
    <StyledLi>
      <QuestionTitle>{title}</QuestionTitle>
      <Text>{text}</Text>
      {imgs
        ? imgs.map((img) => (
            <img src={img.x1} srcSet={`${img.x1} 1x, ${img.x2} 2x`} alt="" />
          ))
        : null}
    </StyledLi>
  );
};

export default Questions;
